<template>
  <div class="profile">
    <Navbar>
      <a v-for='(i) in links' :key="i.msg" @click ='profileDetail(i.type)' :class="{right:i.isRight}">{{i.msg}}</a>
    </Navbar>
    <router-view/>
  </div>
</template>

<script>
import Navbar from '../../components/Navbar.vue'
// @ is an alias to /src



export default {
  components: { Navbar },
  name: 'Profile',
  data(){
    return{
      links:[
            {type:'me', msg: '关于我自己', },
            {type:'page', msg: '关于这个网页', },
            {type:'work', msg: '关于工作', },
            {type:'skill', msg: '关于技能', },
        ]
    }
  },    
  methods:{
        profileDetail(type){
            this.$router.push({path:'/profile/partprofile',query: {type:type}})
        },
        refresh(){
          window.scrollTo(0,0);
        }
  },
  watch:{
    '$route': 'refresh'
  },
  
}

</script>
